@import "App.scss";

.modal-wrapper {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $dark-curtain;

  z-index: 1000;
  opacity: 1;

  transition: all 0.3s ease-in-out;

  &.hidden {
    visibility: hidden;
    z-index: -1;
    opacity: 0;
  }
}

.modal {
  width: min(580px, 100vw - 2 * $gutter);
  max-height: 100%;
  overflow: auto;

  box-shadow: 0 0 10px 5px rgba($black, 0.7);
  margin: 0;

  h1 {
    color: $white;
    margin-bottom: 1em;
  }

  p {
    line-height: 1.5em;
    margin-bottom: 1em;
  }

  strong {
    color: $white;
  }

  code {
    font-family: $mono-font;
    color: $white;
    font-weight: bold;
  }

  .modal-text {
    margin-bottom: 2em;
  }

  .modal-close-button {
    font-weight: bold;
    display: block;
    margin: auto;
  }
}
