@import "App.scss";

.search-body-box,
.search-results-box {
  position: relative;
  padding: $spacer ($spacer * 1.5);
}

.search-curtain {
  position: absolute;
  display: block;
  z-index: 500;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $light-curtain;
}

.search-curtain-off {
  display: none;
}
